___CSS_LOADER_EXPORT___.push([module.id, `body {
    background-color: rgb(125, 54, 10);
    margin: 0;
}

header {
    background-color: rgb(255, 238, 172);
    padding-top: 12px;
    padding-bottom: 12px;
}

nav {
    display: flex;
    justify-content: center;
    gap: 32px;
}

nav>button {
    background-color: transparent;
    border-color: transparent;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 24px;
    color: rgb(125, 54, 10);
    cursor: pointer;
}

#content {
    background-color: rgb(255, 238, 172);
    margin: 48px 15%;
    padding: 16px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: center;
    border-radius: 25px;
}

.logo {
    background-image: url("https://i.ibb.co/9qn4m90/file.png");
    height: 96px;
    width: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 10px;
    background-position: center;
    margin-top: 16px;
}

#content>h1 {
    margin-top: 48px;
}

.quote-bg {
    background-color: rgb(63, 40, 17);
    color: rgb(255, 238, 172);
    padding: 24px 48px;
    font-size: 24px;
    line-height: 1.5;
    margin: 48px 15%;
}

#content ul {
    list-style-type: none;
    text-align: left;
